// Alert iOS Popup
@media (prefers-color-scheme: dark) {
    .ios input {
        --ion-text-color: #FFFFFF;
        --ion-text-color-rgb: 255, 255, 255;
        color: var(--ion-text-color);
    }

    .md input {
        --ion-text-color: #FFFFFF;
        --ion-text-color-rgb: 255, 255, 255;
        color: var(--ion-text-color);
    }
}